<template>
    <div>
        <van-tabbar v-model="active" inactive-color="#666666" active-color="#000000" fixed placeholder>
            <van-tabbar-item replace v-for="(item, index) in tabbarList" :key="index" :to="item.path">
                <span>{{ item.title }}</span>
                <img slot="icon" slot-scope="props" :src="props.active ? item.active : item.normal"/>
            </van-tabbar-item>
        </van-tabbar>
    </div>
</template>

<script>
    export default {
        name: "tabbar",
        data() {
            return {
                active: 0,
                tabbarList: [
                    {
                        path: "/home",
                        title: "首页",
                        normal:
                            "./icon/sy.png",
                        active:
                            "./icon/sy2.png",
                    },
                    {
                        path: "/search",
                        title: "搜索",
                        normal:
                            "./icon/ss.png",
                        active:
                            "./icon/ss2.png",
                    },
                    {
                        path: "/mine",
                        title: "我的",
                        normal:
                            "./icon/wd.png",
                        active:
                            "./icon/wd2.png",
                    },
                ],
            };
        },

        //监听路由变化
        watch: {
            $route(to) {
                this.activeTab(to.path);
            },
        },

        methods: {
            activeTab(path) {
                var index = this.tabbarList.map((item) => item.path).indexOf(path);
                if (index != -1) {
                    this.active = index;
                }
            },
        },
    };
</script>
