import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '*',
    redirect: '/home'  // 默认首页
  },
  {
    path: '/home',
    name: 'home',
    component: () => import('../views/home.vue')//首页
  },
  {
    path: '/mine',
    name: 'mine',
    component: () => import('../views/mine.vue') //我的
  },
  {
    path: '/search',
    name: 'search',
    component: () => import('../views/search.vue') //搜索
  },
  {
    path: '/type',
    name: 'type',
    component: () => import('../views/type.vue') //类型
  },
  {
    path: '/video',
    name: 'video',
    component: () => import('../views/video.vue') //播放视频
  },
]

const router = new VueRouter({
  routes
})

export default router
